* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: inherit; /* Inherit color from parent */
  }

.container {
    z-index: 1;
    position:relative;
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
}

nav {
    position: fixed;
    top: 3%;
    background: white;
    border-radius: 50px;
    padding: 0.5px;
    box-shadow: 0 25px 20px -20px #0066;
}

nav ul li {
    list-style: none;
    display: inline-block;
    margin: 10px;
    font-size: 18px;
    letter-spacing: 4px;
    font-weight: 500;
    color: #000;
    cursor: pointer;
    position: relative;
    z-index: 2;
    transition: color 0.5s;
}

nav ul li a {
    display: block;
    padding: 15px 35px;
}

nav ul li::after {
content: "";
background-color: #000;
width: 100%;
height: 100%;
border-radius: 30px;
position: absolute;
top: 100%;
left: 50%;
transform: translate(-50%, -50%);
z-index: -1;
opacity: 0;
transition: top 0.5s, opacity 0.5s;
}

nav ul li:hover {
color: #fff;
}

a:hover {
    color: #fff;

}

nav ul li:hover::after {
    top: 50%;
    opacity: 1;
}

